<template>
  <div>
    <el-button type="primary" @click="toggleModal">新規</el-button>
    <el-table
      :data="facility"
      stripe
      style="width: 100%">
      <el-table-column
        prop="name"
        label="施設名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="location"
        label="施設場所"
        width="180">
      </el-table-column>
      <el-table-column
        prop="user_id"
        label="ユーザー">
      </el-table-column>

      <el-table-column
        label="Operations">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)">Edit</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- region -->
    <el-pagination></el-pagination>
    <!-- modal -->
    <store-modal :dialogStatus="dialogStatus"
                 :toggleModal="toggleModal"/>
  </div>
</template>

<script>
import StoreModal from '@/components/Facility/Modal/store.vue'
  export default {
    components: {
      StoreModal,
    },
    data() {
      return {
        dialogStatus: false,
        facility: [{
          name: 'ドミトリー１',
          location: 10,
          user_id:8,
        }]
      }
    },
    methods: {
      init () {
        this.axios.get('/api/v1/facilities')
        .then(res => {
          this.facility = res.data
          console.log('success', res)
        })
        .catch(error => {
          console.log('error', error)
        })
      },
      toggleModal () {
        this.dialogStatus = !this.dialogStatus
      },
      handleEdit(index, row) {
        console.log(index, row);
      },
      handleDelete(index, row) {
        console.log(index, row);
      }
    },
    created() {
      this.init()
    }
  }
</script>